.main-container{
    padding:0 15%;
    display:flex;
    align-items: center;
}

.main-content, .main-image{
    width:50%;
}

.line1{
    font-size: 5.5rem;
    font-weight:800;
    line-height: 6rem;
}

.line2{
    padding:5% 0;
}

.line3{
    display: flex;
}

.line3 .category-button{
    background-color: white;
    color:black;
    border:0.6px solid;
}

.line3 .category-button:hover{
    color:white;
    background-color:black;
    transition: 0.3s;
}

.main-image{
    width:50%;
}

.main-image img{
    height:100%
}

.line1{
    animation:vh 0.8s ease-in-out;
    position:relative
}

@keyframes vh{
    0%{left:-700px}
    100%{left:0px}
}

.line2, .line3{
    animation: vh2 1.3s ease-in-out;
}

@keyframes vh2{
    0%{opacity: 0}
    100%{opacity: 1}
}

@media(max-width:980px){
    .main-container{
        flex-direction: column;
    }
    .line1{
        font-size:8rem;
        line-height: 9rem;
    }
    .line2{
        font-size: 2rem;
    }
    .line3{
        flex-direction: column;
    }
    .line3 button{
        font-size: 3rem;
        margin:1% 0;
        border-radius: 5px;
    }
    .main-content, .main-image{
        width:80%;
    }
    .line3 .category-button{
        border: none;
    }
}