.navbar{
    display:flex;
    width:100%;
    justify-content: space-between;
    padding:1.8% 6%;
    align-items: center;
}

.navbar ul{
    display:flex;
    list-style: none;
}

.navbar ul li{
    padding:0 2rem;
    font-weight: 600;
    cursor:pointer;
}

button{
    background-color: red;
    color: white;
    padding: 0.5rem 2rem;
    border:none;
    cursor:pointer;
    
}

.navbar img{
    width:70px
}

.navbar ul li:hover{
    color:red;
    transition: 0.2s;
}

@media(max-width:980px){
    .navbar ul{
        display:none;
    }
    .navbar img{
        width:140px
    }
    .navbar button{
        font-size: 2rem;
    }
}